import React from 'react';
import SliderMain from './SliderMain';
import {  useState } from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faArrowDown } from '@fortawesome/free-solid-svg-icons';
import pdf from '../assets/files/Broker_Tosi_solicitud_alquiler.pdf';
import FormContact from './FormContact';

function Services() {
    const [appl, setAppl] = useState(false);
    const [owner, setOwner] = useState(false);
    const [tasar, setTasar] = useState(false);
    const applicant = () => {
        setAppl(!appl);
    }
    const ownerShow = () => {
        setOwner(!owner);
    }
    const tasarShow = () => {
        setTasar(!tasar);
    }
    return (
        <React.Fragment>

            <SliderMain />
            <h2>Servicios</h2>
            <div className='servicio'>
                <div className='contenedorServicio'>
                    <div className='title'>
                        <h5>Venta</h5>
                    </div>
                    <div>
                        <p>Descubra su hogar ideal con Mnica Tosi, su confiable broker inmobiliario en Mendoza. </p>
                        <p>Ofrecemos un servicio excepcional, brindando una experiencia transparente, segura y personalizada en la compra o venta de propiedades.</p>
                        <p>Nuestro equipo de expertos trabajar de cerca con usted, comprendiendo sus necesidades y utilizando nuestra amplia red de contactos para encontrar las mejores opciones.</p>
                        <p>Valoramos la confianza de nuestros clientes y nos comprometemos a proteger sus intereses, cumpliendo con todas las regulaciones legales. Adems, brindamos un servicio postventa excepcional y estamos disponibles para responder a sus preguntas y proporcionar asesoramiento. Confe en nosotros para hacer realidad sus sueos inmobiliarios.</p>
                    </div>
                </div>
                <div className='contenedorServicio'>
                    <div className='title'>
                        <h5 >Administracin de alquileres</h5>
                    </div>
                    <div>

                        <label>Requisitos para alquilar: Del Solicitante.</label>
                        <button className='btn' onClick={applicant}><FontAwesomeIcon icon={faArrowDown} /> </button>

                        {(appl) &&
                            <>
                                <div>

                                    <p>Para alquilar una propiedad en la provincia de Mendoza, generalmente se requieren los siguientes requisitos:</p>
                                    <p>Documentacin personal: Es necesario presentar el DNI (Documento Nacional de Identidad) o pasaporte vlido del solicitante y de los eventuales fiadores o garantes.</p>
                                    <p>Comprobante de ingresos: Se suele solicitar la presentacin de recibos de sueldo o constancia de ingresos que demuestren la capacidad de pago del alquiler. En algunos casos, puede ser necesario contar con una antigedad laboral mnima.</p>
                                    <p>Garanta: La mayora de los contratos de alquiler en Mendoza requieren de una garanta, que puede ser un garante propietario dentro de la provincia, una garanta bancaria o un seguro de caucin. Esto se solicita como respaldo en caso de incumplimiento de pago del alquiler.</p>
                                    <p>Referencias personales: Algunos propietarios o inmobiliarias pueden solicitar referencias personales o laborales para evaluar la idoneidad del inquilino.</p>
                                    <p>Depsito o fianza: Es comn que se solicite el pago de un depsito o fianza equivalente a uno o dos meses de alquiler, que ser devuelto al finalizar el contrato en caso de no existir daos en la propiedad.</p>
                                    <p>Estos requisitos pueden variar dependiendo del propietario, inmobiliaria o la ubicacin especfica de la propiedad en la provincia de Mendoza..</p>
                                </div>
                            </>}

                        <label>Requisitos para alquilar: Del propietario.</label>
                        <button className='btn' onClick={ownerShow}><FontAwesomeIcon icon={faArrowDown} /> </button>
                        {(owner) &&
                            <>
                                <div>
                                    <p>Cuando un propietario decide dejar su casa en alquiler a travs de una inmobiliaria en la provincia de Mendoza, generalmente se le solicitarn los siguientes requisitos:</p>
                                    <p>Documentacin de propiedad: El propietario deber presentar la documentacin que acredite la propiedad del inmueble, como escritura, ttulo de propiedad, planos, entre otros.</p>
                                    <p>Informacin personal: Se requerir la presentacin de la documentacin personal del propietario, como DNI (Documento Nacional de Identidad) o pasaporte vlido, para verificar su identidad y establecer la relacin contractual.</p>
                                    <p>Condiciones y requisitos del inmueble: El propietario deber brindar informacin detallada sobre las caractersticas y condiciones de la propiedad, incluyendo la superficie, distribucin, servicios disponibles, estado general, y cualquier otra especificacin relevante. Se le solicitara visitar la propiedad para tomar fotos.</p>
                                    <p>Precio y condiciones de alquiler: El propietario y la inmobiliaria acordarn el precio de alquiler mensual y las condiciones especficas del contrato, como plazo de alquiler, formas de pago, actualizacin de precios, entre otros aspectos.</p>
                                    <p>Exclusividad y comisin: La inmobiliaria  solicita exclusividad en la gestin del alquiler, lo cual significa que el propietario se compromete a trabajar exclusivamente con nuestra agencia. Adems, se acordar una comisin o honorarios a pagar a la inmobiliaria por su intermediacin en el proceso de alquiler.</p>
                                </div>
                            </>}
                    </div>
                    <div>
                    <button className='btn-color'>        
                    <a href={pdf} target="_blank" rel="noopener noreferrer" download="Broker_Tosi_solicitud_alquiler.pdf"> 
                     Descargar Requisitos 
                    </a></button>
</div>
                    </div>
                    <div className='contenedorServicio'>
                        <div className='title'>
                            <h5>Tasacin de Propiedades</h5>
                        </div>
                        <div>
                            <p>Obtenga una tasacin precisa y confiable.</p>
                            <p>Ofrecemos un servicio excepcional para determinar el valor justo de su propiedad.</p>
                            <p>Nuestro equipo de expertos utilizar metodologas rigurosas y su profundo conocimiento del mercado local para brindarle una tasacin precisa y objetiva.</p>
                            <p>Valoramos la confianza de nuestros clientes y nos comprometemos a ofrecer un servicio transparente y profesional. Confe en nosotros para obtener una tasacin confiable que respalde sus decisiones financieras y comerciales.</p>
                        </div>
                        <label>Tasar Propiedad</label>
                        <button className='btn' onClick={tasarShow}><FontAwesomeIcon icon={faArrowDown} /> </button>

                        {(tasar) &&
                            <>
                                <FormContact/>
                            </>}
                    </div>
                </div>
            
        </React.Fragment >
    )
}



export default Services;